import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {handleGeneralBarItemClick} from '../../../../../utilities/src/common_helper';

export default class PickupBanner extends Component {
    constructor(props) {
        super(props);
        this.bannerImageHeight = 43;
        platform_api_root.runtime.sendMessage({ method: "google_analytics", value: this.props.google_analytics_display });
    }

    componentDidMount() {
      this.link.addEventListener("click", (e) => {
          handleGeneralBarItemClick(e, this.props.google_analytics_click);
      });
    }

    render() {
        const bannerImageStyle = {
            height: "43px",
            width: "125px"
        }

        // Dynamically calculating the margin to center the image.
        const bannerStyle = {
            display: "inline-block",
            paddingLeft:"10px",
            verticalAlign: "top",
            paddingTop: parseInt(((this.props.bar_height-this.bannerImageHeight)/2)) + "px",
            marginTop: this.props.margin_adjustment + "px"
        }

        const _url =
            "https://toolbar.rakuten.co.jp/intro/everyday/members/campaign1/?l-id=2times_" +
            __WEBPACK_PLATFORM_CODE_FOR_PICKUPBAR_BANNER__ +
            "_pickup_2005";
        return (
            <a href={_url} style={bannerStyle} ref={c => this.link = c}>
                <img src="/img/pickupbar_banner.gif" style={bannerImageStyle}></img>
            </a>
        );
    }
}

PickupBanner.propTypes = {
    bar_height: PropTypes.number,
    margin_adjustment: PropTypes.number,
    google_analytics_display: PropTypes.string,
    google_analytics_click: PropTypes.string
}